/* END Mood */
// wrap in this class to have at least 0.2.0 specificity
// to overpower attribute selectors
.util {
  /* Hidden switchers */
  &.hiddenswitch {
    padding: 0;

    & > .hiddenlabel {
      padding: 1em;
      color: var(--colour0-primary);
    }

    &:active > .hiddenlabel {
      color: var(--colour0-secondary)
    }
  }

  &.hiddenswitcher {
    display: none;

    & + * {
      visibility: hidden;
      opacity: 0;
      max-width: 0;
      max-height: 0;
      padding: 0;
      margin: 0;
      transition-property: visibility, opacity;
    }

    &:checked + * {
      visibility: visible;
      opacity: 1;
      max-width: 100%;
      max-height: 100%;
      padding: inherit;
      margin: inherit;
    }
  }
  /* END Hidden switchers */

  /* Radio inputs fieldset */
  &.radioset {

    & .radiowrapper {
      display: flex;
      flex-flow: row nowrap;
      align-items: center;
    }

    & radiolabel {
      font-size: 1.5em;
    }

    & radiopoint {
      position: relative;
      appearance: none;
      border-radius: 50%;
      border: 1em solid transparent;
      transition-property: all;
      transition-duration: var(--duration-global);
      transition-timing-function: linear;

      &:checked {
        border: 1em solid var(--colour0-primary);
        box-shadow: none;
      }
    }
  }
  /* END Radio inputs fieldset */

  /* File input */
  &.file {
    position: relative;
    text-align: center;

    /* 
      Make file input completely transparent due to limited styling, but not hide it for accesibility reasons. 
      Its label gets styled as a button instead.
    */
    input {
      position: absolute;
      top: 50%;
      left: 50%;
      z-index: -1;
      min-width: 1px;
      min-height: 1px;
      max-width: 1px;
      max-height: 1px;
      opacity: 0;
      margin: 0;
      padding: 0;

      &:focus + label {
        opacity: 1;
      }
    }

    label {
      display: inline-block;
      width: 100%;
      border-radius: 5px;
      border: var(--length-nano) solid var(--colour0-primary);
      padding: var(--length-short);
      opacity: 0.7;

      &:hover {
        opacity: 1;
      }
    }
  }
  /* END File input */

  /* Mood */
  &.buttons {
    text-align: center;
    margin: 1em 0;

    & > button {
      width: auto;
      margin: 0 1em;
    }
  }

  &.positive {
    @include gradientButton(
      var(--colour-positive-primary),
      var(--colour-positive-secondary)
    );
  }

  &.negative {
    @include gradientButton(
      var(--colour-negative-primary),
      var(--colour-negative-secondary)
    );
  }
}