/* Theme switch */
.gtheme {

  .gtheme-switch:checked + & {
    --colour0-primary: hsl(0, 0%, 80%);
    --colour0-secondary: hsl(0, 0%, 20%);
    --colour0-tertiary: hsl(0, 0%, 90%);
    --colour0-quarternary: hsl(0, 0%, 10%);

    --colour-submit-primary: hsl(220, 100%, 80%);
    --colour-submit-secondary: hsl(240, 100%, 50%);
    --colour-positive-primary: hsl(140, 100%, 80%);
    --colour-positive-secondary: hsl(140, 100%, 50%);
    --colour-negative-primary: hsl(350, 100%, 80%);
    --colour-negative-secondary: hsl(350, 100%, 50%);

    --colour-anchor-primary: hsl(120, 100%, 75%);
    --colour-anchor-secondary: hsl(120, 80%, 35%);

    --colour-anchor-local-primary: hsl(270, 100%, 80%);
    --colour-anchor-local-secondary: hsl(270, 80%, 50%);


    & *:focus {
      outline-color: var(--colour0-primary);
    }
  }
}
/* END Theme switch */