.gheader {
  color: var(--colour0-tertiary);
  background-color: var(--colour0-quarternary);
}

.gmain {
  color: var(--colour0-primary);
  background-color: var(--colour0-secondary);
}

.gfooter {
  color: var(--colour0-quarternary);
  background-color: var(--colour0-tertiary);
}

.glogo {
  font-size: 1.3em;
}

.gnav {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-content: center;
  gap: 1em;

  & .glink {
    font-size: 1.5em;
  }
}

.gaddress {
  display: flex;
  flex-flow: row wrap;
  justify-content: start;
  align-content: center;
  gap: 1em;
}

.ghtheme-switch {
  display: none;
}

.gtheme-switcher {
  text-align: end;
  padding: 0;

  & label {
    color: var(--colour0-primary);
    background-color: var(--colour0-secondary);
    border-radius: 50%;
    border: 1px solid;
    padding: 0.5em;
  }
}