section.previews {
  & .gradient {
    width: 5em;
    height: 5em;

    position: relative;
    background-image: radial-gradient(
      circle,
      hsl(280, 100%, 50%),
      hsl(179, 100%, 30%)
    );
    z-index: 0;

    &::before {
      position: absolute;
      z-index: -1;
      content: "";
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background-image: radial-gradient(
        circle,
        hsl(344, 100%, 50%),
        hsl(31, 100%, 40%)
      );
      transition-property: opacity;
      transition-timing-function: linear;
      opacity: 0;
    }

    &:hover::before {
      opacity: 1;
    }
  }

  & .form-examples {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: stretch;
    gap: 1em;
  }
}
